.gteviz-wrapper {
  font-family: "Source Sans Pro", sans-serif;
  display: block;
  width: 100%;
  position: relative; }
  .gteviz-wrapper .gteviz-inner {
    display: block;
    position: relative; }
    .gteviz-wrapper .gteviz-inner:after {
      display: block;
      content: '';
      clear: both; }
  .gteviz-wrapper .gteviz-anchor {
    display: block;
    height: 0; }
